<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <v-layout
      v-if="!loading"
      row
      wrap>
      <v-flex md12>
        <v-form
          v-model="valid"
          novalidate
          @submit.prevent="validateBeforeSubmit">
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Identificação</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <v-autocomplete
                    v-validate="'required'"
                    v-model="form.empresaId"
                    :items="empresas"
                    :error-messages="errors.collect('empresa')"
                    :disabled="filtros.lojaId != null"
                    label="Empresa"
                    name="empresa"
                    item-value="id"
                    item-text="nome"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-mask="'##.###.###/####-##'"
                    v-validate="'required'"
                    v-model.trim="form.cnpj"
                    :error-messages="errors.collect('CNPJ')"
                    :return-masked-value="false"
                    :disabled="filtros.lojaId != null"
                    label="CNPJ"
                    type="text"
                    data-vv-name="CNPJ"
                    required
                  />
                </v-flex>
                <v-flex md5>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.razaoSocial"
                    :error-messages="errors.collect('Razao Social')"
                    label="Razao Social"
                    data-vv-name="Razao Social"
                    required
                  />
                </v-flex>
                <v-flex md5>
                  <v-text-field
                    v-model="form.nomeFantasia"
                    label="Nome Fantasia"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-mask="['(##) #### - ####', '(##) ##### - ####']"
                    v-validate="'required'"
                    v-model="form.telComercial"
                    :error-messages="errors.collect('Telefone Comercial')"
                    label="Telefone Comercial"
                    type="text"
                    data-vv-name="Telefone Comercial"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-mask="['(##) ##### - ####']"
                    v-model="form.whatsapp"
                    label="Whatsapp"
                    type="text"
                    prepend-icon="mdi-whatsapp"
                  />
                </v-flex>

                <v-flex md4>
                  <v-text-field
                    v-model="form.pessoaContato"
                    label="Pessoa Contato"
                    type="text"
                  />
                </v-flex>

                <v-flex md4>
                  <v-text-field
                    v-model="form.site"
                    label="Site"
                    type="text"
                  />
                </v-flex>

                <v-flex md3>
                  <v-autocomplete
                    v-validate="'required'"
                    v-model="form.ramoAtividadeId"
                    :items="ramosAtividade"
                    :error-messages="errors.collect('ramoAtividade')"
                    :disabled="filtros.lojaId != null"
                    label="Ramo Atividade"
                    name="ramoAtividade"
                    item-text="descricao"
                    item-value="id"
                  />
                </v-flex>
                <v-flex md7>
                  <v-text-field
                    v-validate="'email'"
                    v-model="form.email"
                    :error-messages="errors.collect('E-mail')"
                    label="Email"
                    type="email"
                    data-vv-name="E-mail"
                  />
                </v-flex>
                <v-flex md2>
                  <v-autocomplete
                    v-validate="'required'"
                    v-model="form.status"
                    :items="listaStatus"
                    :error-messages="errors.collect('status')"
                    label="Status"
                    name="status"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="form.chaveTEF"
                    label="Chave TEF"
                    type="text"
                    readonly
                    hint="A chave TEF é gerada automaticamente pelo sistema, não precisa preenchê-la."
                  />
                </v-flex>

                <v-flex md3>
                  <v-checkbox
                    v-model="form.emitirCartaoLoja"
                    label="Emitir cartão loja"
                  />
                </v-flex>
                <v-flex md3>
                  <v-checkbox
                    v-model="form.envioSMSBoasVindas"
                    label="Enviar SMS de boas vindas"
                  />
                </v-flex>
                <v-flex md4>
                  <v-checkbox
                    v-model="form.permitirPagamentoLoja"
                    label="Permitir pagamento de fatura/recarga na loja"
                  />
                </v-flex>
                <v-flex md4>
                  <v-checkbox
                    v-model="form.permitirAutorizacaoCompra"
                    label="Permitir autorização de compra no APP"
                  />
                </v-flex>
                <v-flex md3>
                  <v-checkbox
                    v-model="form.possuiLimiteExclusivo"
                    label="Possui limite exclusivo"
                  />
                </v-flex>
                <v-flex md12>
                  <v-textarea
                    v-model="form.observacoes"
                    name="observacao"
                    label="Observações"
                    hint="Observações"
                  />
                </v-flex>

                <v-flex md12>
                  <v-layout column>
                    <span class="body-2">Logo da Loja</span>
                    <v-image-input
                      v-model="logo"
                      :image-quality="0.85"
                      :image-height="400"
                      :image-width="600"
                      image-min-scaling="contain"
                      class="arrow-pagintion"
                      clearable
                      image-format="jpeg"
                    />
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>

          <br>

          <core-endereco
            v-model="form.endereco"
            :disabled="disabled"
          />

          <br>

          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Terminais POS/TEF</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md2>
                  <v-text-field
                    v-model="form.terminal.terminal1"
                    label="Terminal 1"
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-model="form.terminal.serial1"
                    label="Número de Série"
                  />
                </v-flex>
                <v-flex md6>
                  <v-text-field
                    v-model="form.terminal.observacao1"
                    label="Observação"
                  />
                </v-flex>

                <v-flex md2>
                  <v-text-field
                    v-model="form.terminal.terminal2"
                    label="Terminal 2"
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-model="form.terminal.serial2"
                    label="Número de Série"
                  />
                </v-flex>
                <v-flex md6>
                  <v-text-field
                    v-model="form.terminal.observacao2"
                    label="Observação"
                  />
                </v-flex>

                <v-flex md2>
                  <v-text-field
                    v-model="form.terminal.terminal3"
                    label="Terminal 3"
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-model="form.terminal.serial3"
                    label="Número de Série"
                  />
                </v-flex>
                <v-flex md6>
                  <v-text-field
                    v-model="form.terminal.observacao3"
                    label="Observação"
                  />
                </v-flex>

                <v-flex md2>
                  <v-text-field
                    v-model="form.terminal.terminal4"
                    label="Terminal 4"
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-model="form.terminal.serial4"
                    label="Número de Série"
                  />
                </v-flex>
                <v-flex md6>
                  <v-text-field
                    v-model="form.terminal.observacao4"
                    label="Observação"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>

          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Campanhas</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <v-checkbox
                    v-model="form.campanhaAtiva"
                    label="Possui Campanha Ativa"
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-model="form.campanhaQtdVendas"
                    label="Número de vendas"
                    suffix="dias"
                    step="1"
                    min="0"
                    type="number"
                    class="text-field-custom"
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-model="form.campanhaQtdDiasPrimeiraParcela"
                    label="Primeira parcela"
                    suffix="dias"
                    step="1"
                    min="0"
                    type="number"
                    class="text-field-custom"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>

          <br>

          <v-alert
            :value="true"
            type="warning">
            <span
              class="subheading"
            >As regras cadastradas na Loja substitui as regras da Empresa, caso não seja cadastrado regras aqui, será utilizado a da Empresa.</span>
          </v-alert>

          <br>

          <template v-if="acao === acaoListar">
            <v-card>
              <v-toolbar
                flat
                color="white">
                <v-toolbar-title>Regras da Loja</v-toolbar-title>
                <v-divider
                  class="mx-2"
                  inset
                  vertical />
                <v-spacer />
                <v-btn
                  small
                  fab
                  color="primary">
                  <v-icon @click="newRegra()">mdi-plus</v-icon>
                </v-btn>
              </v-toolbar>
              <v-progress-linear
                :active="loading"
                color="light-blue"
                indeterminate
                height="3"
              />
              <v-data-table
                :headers="headers"
                :items="regras"
                :total-items="totalItems"
                :pagination.sync="pagination"
                class="elevation-1 arrow-pagintion"
              >
                <template
                  slot="items"
                  slot-scope="{ item }"
                  ma-5
                >
                  <core-tr-item-regra
                    :item="item"
                    :visualizar-regra="visualizarRegra"
                    :alterar-regra="editarRegra"
                    :reload="reload"
                  />
                </template>
              </v-data-table>
            </v-card>
            <v-layout class="justify-end">
              <template>
                <v-btn
                  outline
                  style="color: black !important"
                  @click="cancelar">Cancelar</v-btn>
                <v-btn
                  :disabled="!valid"
                  :loading="loadingBtn"
                  type="submit"
                  color="primary">Salvar</v-btn>
              </template>
            </v-layout>
          </template>

          <template v-else>
            <v-card
              v-if="!disabled"
              class="elevation-0">
              <v-card-text>
                <v-autocomplete
                  v-model="copyId"
                  :items="empresas"
                  item-value="id"
                  item-text="nome"
                  label="Copiar dados da regra vigente de outra empresa"
                  @change="carregaDadosCopy"
                />
              </v-card-text>
            </v-card>
            <core-regra-financeira
              v-model="regra"
              :disabled="disabled"
              :salvar="validateBeforeSubmit"
              :voltar="voltar"
              :cancelar="cancelar"
              :acao="acao"
              :primeira-regra="false"
            />
          </template>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {
  EmpresaBusiness,
  LojaBusiness,
  TiposBusiness,
  AnexoBusiness,
  RegraFinanceiraBusiness
} from '../../business'
import RegraFinanceira from '../../beans/RegraFinanceira'
import Endereco from '../../beans/Endereco'
import {
  ACAO_NOVA_REGRA,
  ACAO_VISUALIZAR_REGRA,
  ACAO_LISTAR_REGRA, ACAO_EDITAR
} from '../../constants'
import dateUtils from '../../utils/dateUtils'

export default {
  metaInfo: {
    titleTemplate: '%s - Loja'
  },
  data () {
    return {
      form: {
        id: null,
        empresaId: null,
        razaoSocial: null,
        nomeFantasia: null,
        cnpj: null,
        status: 'ATIVO',
        telComercial: null,
        chaveTEF: null,
        pessoaContato: null,
        site: null,
        email: null,
        whatsapp: null,
        ramoAtividadeId: null,
        emitirCartaoLoja: false,
        envioSMSBoasVindas: false,
        permitirPagamentoLoja: false,
        permitirAutorizacaoCompra: false,
        observacoes: null,
        campanhaAtiva: null,
        campanhaQtdVendas: null,
        campanhaQtdDiasPrimeiraParcela: null,
        planoPagamentoId: null,
        logoId: null,
        terminal: {
          id: null,
          terminal1: null,
          serial1: null,
          observacao1: null,
          terminal2: null,
          serial2: null,
          observacao2: null,
          terminal3: null,
          serial3: null,
          observacao3: null,
          terminal4: null,
          serial4: null,
          observacao4: null
        },
        endereco: new Endereco()
      },
      copyId: null,
      regra: new RegraFinanceira(),
      headers: [
        { class: 'grey lighten-1', sortable: false, text: 'Vigente', align: 'center' },
        { class: 'grey lighten-1', sortable: false, text: 'Vigente Desde', value: 'dtVigencia' },
        { class: 'grey lighten-1', sortable: false, text: 'Multa atraso', align: 'right', value: 'peMultaPorAtraso' },
        { class: 'grey lighten-1', sortable: false, text: 'Juros Mora atraso', align: 'right', value: 'peJurosMoraPorAtraso' },
        { class: 'grey lighten-1', sortable: false, text: 'Tarifa Parcela Banco', align: 'right', value: 'vlTarifaParcelaBanco' },
        { class: 'grey lighten-1', sortable: false, text: 'Tarifa Parcela Loja', align: 'right', value: 'vlTarifaParcelaLoja' },
        { class: 'grey lighten-1', sortable: false, text: 'Cobrar Carnê em Loja', align: 'right', value: 'cobrarCarneLoja' },
        { class: 'grey lighten-1', sortable: false, text: 'Incluída Por', value: 'nomeUsuario' },
        { class: 'grey lighten-1', sortable: false, text: '', value: '', align: 'center' }
      ],
      pagination: {
        rowsPerPage: 10,
        sortBy: 'dataVigencia'
      },
      filtros: {
        lojaId: null
      },
      loading: false,
      logo: null,
      regras: [],
      totalItems: 0,
      valid: false,
      redes: [],
      acao: ACAO_LISTAR_REGRA,
      disabled: false,
      acaoListar: ACAO_LISTAR_REGRA,
      listaStatus: [],
      empresas: [],
      ramosAtividade: [],
      planosPagamento: [],
      loadingCidades: false,
      loadingBtn: false
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  beforeMount () {
    this.reload()
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.salvarLoja()
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    salvarLoja () {
      this.loadingBtn = true
      var msgSucesso = !this.form.id
        ? 'Cadastro realizado com sucesso'
        : 'Atualização realizada com sucesso'
      LojaBusiness.salvar(this.form)
        .then(response => {
          this.form.id = response.data.id
          var promiseLogo = null
          var promiseRegra = null
          // eslint-disable-next-line no-extra-boolean-cast
          if (!!this.logo) {
            let formData = new FormData()
            formData.append('file', this.logo)
            formData.append('nome', 'loja-logo')

            promiseLogo = LojaBusiness.uploadLogo(
              this.form.id,
              formData
            )
          }
          if (!!this.regra && !this.regra.id) {
            // só pode salvar a regra se ela for nova, ou seja, id dela é null
            this.regra.lojaId = this.form.id
            promiseRegra = RegraFinanceiraBusiness.salvar(this.regra)
          }

          Promise.all([
            promiseLogo,
            promiseRegra
          ]).then(() => {
            this.$root.showSucesso(msgSucesso)
            this.$router.push('/lista-loja')
          }).catch(error => this.$root.showErro(error.data))
            .finally(() => {
              this.loadingBtn = false
            })
        })
        .catch(error => {
          this.loadingBtn = false
          this.$root.showErro(error.data)
        })
    },
    cancelar () {
      this.$router.push('/lista-loja')
    },
    voltar () {
      this.reset()
    },
    visualizarRegra (regra) {
      this.disabled = true
      RegraFinanceiraBusiness.getById(regra.id)
        .then(response => {
          this.regra = response.data
          this.regra.dataVigencia = this.$moment(response.data.dataVigencia).format('YYYY-MM-DD')
          this.regra.dataVigenciaLancamento = this.$moment(response.data.dataVigenciaLancamento).format('YYYY-MM-DD')
          this.acao = ACAO_VISUALIZAR_REGRA
        })
        .catch(() => this.$root.showAlerta('Problemas para recuperar os dados da regra, tentar mais tarde')
        )
    },
    editarRegra (regra) {
      RegraFinanceiraBusiness.getById(regra.id)
        .then(response => {
          this.regra = response.data
          this.regra.dataVigencia = this.$moment(response.data.dataVigencia).format('YYYY-MM-DD')
          if (response.data.dataVigenciaLancamento) {
            this.regra.dataVigenciaLancamento = this.$moment(response.data.dataVigenciaLancamento).format('YYYY-MM-DD')
          }
          this.acao = ACAO_EDITAR
        })
        .catch(() => this.$root.showAlerta('Problemas para recuperar os dados da regra, tentar mais tarde'))
    },
    newRegra () {
      this.acao = ACAO_NOVA_REGRA
      this.regra = new RegraFinanceira()
    },
    paginar () {
      if (!this.filtros.lojaId) return
      RegraFinanceiraBusiness.pagination(
        this.pagination.rowsPerPage,
        this.pagination.page - 1,
        this.pagination.sortBy || 'dataVigencia',
        this.filtros
      )
        .then(response => {
          this.regras = response.data.content
          this.totalItems = response.data.totalElements
        })
        .catch(() => this.$root.showErro('Erro ao carregar as regras da rede'))
    },
    reset () {
      this.acao = ACAO_LISTAR_REGRA
      this.disabled = false
      this.regra = null
    },
    reload () {
      this.reset()
      this.filtros.lojaId = this.$route.params.id
      // eslint-disable-next-line no-extra-boolean-cast
      if (!!this.filtros.lojaId) {
        this.loading = true
        LojaBusiness.getById(this.filtros.lojaId)
          .then(response => {
            this.form = response.data
            if (!this.form.endereco) {
              this.form.endereco = new Endereco()
            }
            // eslint-disable-next-line no-extra-boolean-cast
            if (!!this.form.logoId) {
              AnexoBusiness.getById(this.form.logoId).then(response => {
                this.logo = response.data.strBase64
              })
            }
            this.paginar()
          })
          .catch(() => this.$root.showErro('Erro ao buscar informações da loja'))
          .finally(() => {
            this.loading = false
          })
      }

      EmpresaBusiness.findAll()
        .then(response => (this.empresas = response.data))
        .catch(() => this.$root.showErro('Não foi possível buscar as informações das empresas'))

      TiposBusiness.listStatus()
        .then(response => (this.listaStatus = response.data))
        .catch(() => this.$root.showErro('Não foi possível buscar as informações da lista de status'))

      TiposBusiness.listRamoAtividade()
        .then(response => (this.ramosAtividade = response.data))
        .catch(() => this.$root.showErro('Não foi possível buscar as informações da lista de ramo atividade'))

      TiposBusiness.listPlanoPagamento()
        .then(response => (this.planosPagamento = response.data))
        .catch(() => this.$root.showErro('Não foi possível buscar as informações dos planos de pagamentos'))
    },
    carregaDadosCopy (empresaId) {
      this.loading = true
      EmpresaBusiness.getRegrafinanceiraVigenteById(empresaId)
        .then(response => {
          this.regra = { ...response.data }
          this.regra.dataVigencia = dateUtils.addDia(new Date(), 1)
          this.regra.dataVigenciaLancamento = null
          this.regra.id = null
          this.regra.isVigente = false
        })
        .catch(erro => {
          this.$root.showAlerta('Problemas para copiar dados: ' + erro)
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
